@use "global" as *;

.contact {
  position: relative;
}

.contact::before {
  content: "";
  position: absolute;
  top: rem(54);
  right: calc(50% - max(46vw, rem(172.5)));
  min-width: rem(109);
  width: 29.067%;
  aspect-ratio: 109 / 50;
  background-image: url(./../images/common/fish-left-img.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @include mq("md") {
    top: rem(100);
    right: calc(50% - min(48vw, rem(670)));
    max-width: rem(301);
    width: 20.903%;
    aspect-ratio: 301 / 138;
  }
}

.contact__wrap {
  filter: drop-shadow(0 rem(4) rem(4) rgba(0, 0, 0, 0.25));
  @include mq("md") {
    filter: none;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rem(2) rem(2) rem(10) 0 rgba(0, 0, 0, 0.25);
    padding: rem(80) rem(50) rem(79) rem(50);
  }
}

.contact__btn-wrap {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-top: rem(38);
  transform: translate(rem(-1), rem(1));
  @include mq("md") {
    margin-top: rem(40);
    transform: translate(rem(-2), rem(-1));
  }
}
