@use "global" as *;

.voice {
  position: relative;
}

.voice::before {
  @include mq("md") {
    content: "";
    position: absolute;
    top: rem(24);
    left: calc(50% - min(48vw, rem(620)));
    max-width: rem(301);
    width: 20.903%;
    aspect-ratio: 301 / 138;
    background-image: url(./../images/common/fish-right-img.png);
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.voice::after {
  @include mq("md") {
    content: "";
    position: absolute;
    bottom: rem(73);
    right: calc(50% - min(48vw, rem(540)));
    width: 4.930556%;
    max-width: rem(71);
    aspect-ratio: 71 / 162;
    background-image: url(./../images/common/seahorse-img.png);
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.voice__btn-wrap {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-top: rem(40);
  transform: translate(rem(-1), rem(1));
  @include mq("md") {
    margin-top: rem(81);
    transform: translate(rem(-2), rem(-1));
  }
}
